.megamenu  {
  position: absolute;
  top: 90px;
  left: 0;
  background: white;
  width: 100%;
  max-width: 100%;
  box-sizing: border-box;
  overflow: hidden;
  transition: var(--transition-large);
  max-height: 0;
  opacity: 0;
}

.megamenu.active {
  max-height: none;
  opacity: 1;
}

.megamenu a {
  color: black;
  font-size: var(--size-min);
  text-transform: uppercase;
}

.megamenu .menu {
  width: 100%;
}

.megamenu .menu > li {
  text-align: left;
  display: inline-block;
  width: 100%;
}

.megamenu.reset {
  background: white;
}

.megamenu svg {
  fill: black;
}

@media (--screen-small) {
  .megamenu {
    top: 120px;
  }
}

@media (--screen-medium) {

  .megamenu {
    background: linear-gradient(top, white 42px, var(--gray-light) calc(42px - 100%));
  }

  .megamenu .menu {
    display: flex;
    padding: calc(var(--space-large)*2);
    padding-top: var(--space-base);
    padding-left: 0;
  }

  .megamenu .menu-info {
    padding: var(--space-base) !important;
  }

  .megamenu .menu > li {
    width: 50%;
  }

  .megamenu .btn-icon {
    display: none;
  }

  .megamenu .menu-info li {
    text-align: center;
  }

  .megamenu .menu > li:nth-child(1) .dropdown,
  .megamenu .menu > li:nth-child(2) .dropdown {
    column-count: 2;
    width: 70%;
  }

  .megamenu .dropdown {
    margin-top: var(--space-large);
  }

}

@media (--screen-mobile) {

  .megamenu {
    background: none;
  }

  .megamenu .menu {
    line-height: 0;
  }

  .megamenu .container {
    width: 100%;
    max-width: 100%;
    margin: 0;
  }

  .megamenu .menu > li {
    position: relative;
    background: white;
    margin: 0;
    height: 40px;
  }

  .megamenu .item-drop.active button {
    transform: rotateY(180deg);
  }

  .megamenu li:last-child {
    margin-bottom: 0;
  }

  .megamenu .item-drop.inactive {
    display: none;
  }

  .megamenu .btn-dropdown {
    position: absolute;
    display: block;
    top: 0;
    right: 0;
    font-size: var(--size-base);
    width: 40px;
    height: 40px;
  }

  .megamenu .btn-dropdown:--over {
    color: black;
  }

  .megamenu .dropdown {
    background: var(--gray-light);
    transform: translateX(-100%);
    overflow: hidden;
    max-height: 0;
    transition: transform 0.2s;
  }

  .megamenu .dropdown.active {
    max-height: none;
    transform: translateX(0);
    transition: transform 0.2s;
  }

  .megamenu .dropdown li {
    margin: 0;
  }

  .megamenu .dropdown li:first-child a {
    padding-top: 10px;
  }

  .megamenu .dropdown li:last-child a {
    padding-bottom: 10px;
  }

  .megamenu .menu a {
    height: 100%;
    line-height: normal;
    align-items: center;
    justify-content: space-between;
    display: flex;
    padding-left: 6%;
    box-sizing: border-box;
  }

  .megamenu .dropdown a {
    height: 40px;
    display: flex;
    align-items: center;
    padding-left: 6%;
  }

}
